<template>
  <!-- 规则 -->
  <div class="rule">
    <!-- <game-info-comp :showAvatarInfo="false" :headerInfo="headerInfo" :contentInfo="contentInfo"></game-info-comp> -->
    <div class="img-tit">
      <!-- <el-image class="img" :src="titlePng"></el-image> -->
      "第十八届中国日报社社“21世纪杯”小学生英语演讲大会暨第四届少儿英语大会"
    </div>
    <!-- 大会时间 -->
    <div class="time">
      <div>
        <title-comp tit="大会时间"></title-comp>
      </div>
      <div class="item-box">
        <div class="time-item">报名及地区初选（2019年11月-12月）</div>
        <div class="time-item">地区复选（2019年12月-2020年1月）</div>
        <div class="time-item">地区决选（2020年1月-2月）</div>
        <div class="time-item">全国决选（2020年3月-4月）</div>
      </div>
    </div>
    <!-- 组别设置 -->
    <div class="group-set">
      <div>
        <title-comp tit="组别设置"></title-comp>
      </div>
      <div class="group-set-info">本次大会共设四个组别，分别是少儿组、1-2年级组、3年级组、4-6年级组</div>
    </div>
    <!-- 参赛规则 -->
    <div class="game-rule">
      <div>
        <title-comp tit="参赛规则"></title-comp>
      </div>
      <div class="game-rule-box">
        <div class="collapse-box">
          <div class="collapse-tit">
            <div class="text">报名及地区初选（2019/11-2019/12)</div>
            <div class="ic-box" @click="openCollapse(1)">
              <img src="../../../assets/img/arrow.png" class="ic" :class="{rotate: open1}" />
            </div>
          </div>
          <div class="collapse-content" :style="open1?'display: block': 'display:none'">
            <p>(一)参与范围</p>
            <p>
              1.国家承认的具有招生资格的幼儿园、全日制在校学生均可报名参加，年龄不限；
              非中国国籍学生或在英语国家生活、学习过1年以上（含1年）的中国学生不具备参与资格。
            </p>
            <p>2.重庆赛区组委会在地区邀请学校等单位，合作组织现场选拔，直接选拔部分参赛者进入地区复选。</p>
            <p>(二)报名及地区初选</p>
            <p>报名参加地区初选的方式包括线上自主报名参赛、线下集体报名参赛两种。</p>
            <p>1.线上自主报名参赛</p>
            <p>
              参赛者通过官方公众号“第18届世纪杯重庆组委会”（以下简称“官方公众号”）报名，
              报名截止日期：2019年12月12日。报名时，参赛者需要填写参赛者姓名、就读学校、就读年级、
              家长电话等信息，并按要求上传个人照片和参赛视频（视频应为长宽比为4:3的横屏录制视频）。
            </p>
            <p>参赛视频内容要求：</p>
            <p>
              (1)少儿组：自我介绍+英语歌曲、英语绘本、英语故事、英语配音4选1，总时长2分钟
              <br />(2)1-2年级组：自我介绍+英语绘本、英语故事、英语配音、英语戏剧4选1，总时长2分钟
              <br />(3)3年级组：自我介绍+自备主题演讲，总时长2分钟
              <br />(4)4-6年级组：自我介绍+自备主题演讲，总时长3分钟
              <br />
            </p>
            <p>
              2.线下集体报名参赛
              <br />学生在学校或机构报名参加线下地区初选，具体参赛流程以学校和机构通知为准。
              <br />参赛内容要求：
              <br />(1)少儿组：自我介绍+英语歌曲、英语绘本、英语故事、英语配音4选1，总时长2分钟
              <br />(2)1-2年级组：自我介绍+英语绘本、英语故事、英语配音、英语戏剧4选1，总时长2分钟
              <br />(3)3年级组：自我介绍+自备主题演讲，总时长2分钟
              <br />(4)4-6年级组：自我介绍+自备主题演讲，总时长3分钟
              <br />
            </p>
            <p>(三)评判标准及奖励事项</p>
            <p>
              线上报名由评委线上审核并评分，线下报名由评委现场评分，评判标准以重庆赛区组委会通知为准。
              重庆赛区组委会综合初选人数及复选名额限制制定晋级比例，优胜者将晋级地区复选。晋级名单将在官方公众号公布。
            </p>
          </div>
        </div>
        <div class="collapse-box">
          <div class="collapse-tit">
            <div class="text">地区复选（2019/12-2020/1）</div>
            <div class="ic-box" @click="openCollapse(2)">
              <img src="../../../assets/img/arrow.png" class="ic" :class="{rotate: open2}" />
            </div>
          </div>
          <div class="collapse-content" :style="open2?'display: block': 'display:none'">
            <p>(一)复选流程</p>
            <p>线上选择比赛场次</p>
            <p>晋级地区复选的参赛者通过官方公众号选择比赛场次。通过线下集体报名参赛晋级的参赛者需先完成身份认证才能选择比赛场次。</p>
            <p>2.现场比赛</p>
            <p>
              选定场次后准时到现场参加比赛。参赛内容要求（请在本页面底部下载少儿组和1-2年级组地区复选所需绘本）：
              <br />(1)少儿组：自我介绍+指定绘本朗读+评委问答，自我介绍和绘本朗读总时长2分钟
              <br />(2)1-2年级组：自我介绍+指定绘本朗读+评委问答，自我介绍和绘本朗读总时长2分钟
              <br />(3)3年级组：自我介绍+指定命题演讲（If I had super powers）+评委问答，自我介绍和命题演讲总时长2分钟
              <br />(4)4-6年级组：自我介绍+指定命题演讲（Colors of the world）+评委问答，自我介绍和命题演讲总时长3分钟
              <br />
            </p>
            <p>3.线上助力活动</p>
            <p>
              重庆赛区组委会将为每一位参加地区复选的参赛者录制现场比赛视频，并上传至官方公众号。
              <br />选手可通过官方公众号查看视频，并在专题活动版块分享个人参赛视频的海报或链接请朋友为其点赞。
              <br />
            </p>
            <p>
              已晋级地区决选参赛者：根据点赞数每组别选取一名人气大奖。
              <br />未晋级地区决选参赛者：根据点赞数每组别给出五个复活名额，直接晋级地区决选。
              <br />
            </p>
            <p>(二)评判标准及奖励事项</p>
            <p>
              评委现场评分，评判标准以重庆赛区组委会通知为准。
              <br />重庆赛区组委会综合复选人数及决选名额限制制定晋级比例，优胜者将晋级地区决选。晋级名单将在官方公众号公布。
            </p>
          </div>
        </div>
        <div class="collapse-box">
          <div class="collapse-tit">
            <div class="text">地区决选（2020/1-2020/2）</div>
            <div class="ic-box" @click="openCollapse(3)">
              <img src="../../../assets/img/arrow.png" class="ic" :class="{rotate: open3}" />
            </div>
          </div>
          <div class="collapse-content" :style="open3?'display: block': 'display:none'">
            <p>(一)决选流程</p>
            <p>
              晋级地区决选的参赛者，通过官方公众号获取比赛场次信息，
              准时到现场参加比赛。参赛内容要求（请在本页面底部下载少儿组和1-2年级组地区决选所需绘本）：
            </p>
            <p>
              (1)少儿组：自我介绍+指定绘本朗读+评委问答，自我介绍和绘本朗读总时长3分钟
              <br />(2)1-2年级组：自我介绍+指定绘本朗读+评委问答，自我介绍和绘本朗读总时长3分钟
              <br />(3)3年级组：自我介绍+指定命题演讲（If I had super powers）+评委问答，自我介绍和命题演讲总时长3分钟
              <br />(4)4-6年级组：自我介绍+指定命题演讲（Colors of the world）+评委问答，自我介绍和命题演讲总时长4分钟
              <br />
            </p>
            <p>(二)评判标准及奖励事项</p>
            <p>评委现场评分，评判标准以重庆赛区组委会通知为准。具体奖项设置如下：</p>
            <p>
              1.冠军（8名）：每组别2名
              <br />2.亚军（12名）：每组别3名
              <br />3.季军（20名）：每组别5名
              <br />4.单项奖（12名）：每组别 “最佳口语”、“最佳才艺”、“最佳人气”奖各1名
              <br />5.最佳组织奖：参赛学生超100人的合作学校，均可获得重庆赛区组委会授牌
              <br />6.指导教师奖：合作学校老师指导学生入围全国决选，均可获得重庆赛区组委会授予的证书及精美礼品
              <br />
            </p>
          </div>
        </div>
        <div class="collapse-box">
          <div class="collapse-tit">
            <div class="text">全国决选（2020/3-2020/4）</div>
            <div class="ic-box" @click="openCollapse(4)">
              <img src="../../../assets/img/arrow.png" class="ic" :class="{rotate: open4}" />
            </div>
          </div>
          <div class="collapse-content" :style="open4?'display: block': 'display:none'">
            <p>全国决选具体情况以组委会后续通知为准。</p>
          </div>
        </div>
      </div>
    </div>
    <!-- 注意事项 -->
    <div class="notice">
      <div>
        <title-comp tit="注意事项"></title-comp>
      </div>
      <div class="notice-info">
        <p>
          1.参赛者应保证个人信息真实准确，稿件和演讲录像/录音的作者必须一致，
          如发现大段抄袭、稿件雷同等作弊现象，将取消其参与资格。
        </p>
        <p>2.组委会全程不会向参赛者收取任何报名费用，请警惕各种骗局，举报电话17783117458。</p>
        <p>3.对于入围地区复选、地区决选、全国决选的参赛者，组委会将通过短信和官方公众号内通知消息形式通知相应参赛者，敬请关注。</p>
        <p>4.大会全程参赛者的演讲稿、照片及演讲录像/录音版权均归二十一世纪英语教育传媒及重庆赛区组委会所有。</p>
      </div>
    </div>
    <!-- 下载 -->
    <div class="download">
      <div class="btn">点击下载绘本</div>
    </div>
  </div>
</template>
<script>
import mxTitles from '@/components/basic/title.vue'
import titleComp from '../../../components/titleComp/titleComp'
import gameInfoComp from '../../../components/gameInfo/gameInfo'
import titlePng from '../../../assets/img/title.png'
export default {
  data () {
    return {
      activeNames: '',
      open1: false,
      open2: false,
      open3: false,
      open4: false,
      titlePng: titlePng,
      headerInfo: {
        imgUrl:
          '//y.gtimg.cn/music/photo_new/T002R300x300M000003d636i2FSP7z.jpg?max_age=2592000',
        name: 'IKON',
        phone: '13458956354'
      },
      contentInfo: {
        title: '大赛规则',
        text: `我是评委简介我是评委简介我是评委简介我是评委简介
        我是评委简介我是评委简介我是评委简介我是评委简介
        我是评委简介我是评委简介我是评委简介我是评委简介`
      }
    }
  },
  methods: {
    openCollapse (num) {
      // this.open1 = !this.open1
      this['open' + num] = !this['open' + num]
    }
  },
  components: {
    gameInfoComp,
    titleComp,
    mxTitles
  }
}
</script>
<style lang="less">
.rule {
  width: 100%;
  box-sizing: border-box;
  padding: 15px 18px;
  .img-tit {
    font-size: 16px;
    color: #ff5700;
    font-family: PingFang-SC-Heavy;
    font-weight: bold;
    line-height: 14 * 1.5px;
    text-align: center;
  }
  .time {
    margin-top: 15px;
    .item-box {
      // margin-top: 15px;
      margin-top: 5px;
      .time-item {
        margin-top: 5px;
        font-size: 14px;
        color: #333333;
      }
    }
  }
  //组别设置
  .group-set {
    margin-top: 15px;
    .group-set-info {
      margin-top: 5px;
      color: #333333;
      font-size: 14px;
      line-height: 1.4;
    }
  }
  // 参赛规则
  .game-rule {
    margin-top: 15px;
    .game-rule-box {
      margin-top: 0px;
      .collapse-box {
        .collapse-tit {
          width: 100%;
          position: relative;
          padding: 0 5px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          .text {
            font-size: 14px;
            color: #333333;
            letter-spacing: 1px;
          }
          .ic-box {
            padding: 10px;
            .ic {
              width: 13px;
              height: 8px;
              transition: all 0.5s ease;
            }
            .rotate {
              transform: rotate(-180deg);
            }
          }
          &::after {
            position: absolute;
            content: "";
            bottom: 0;
            left: 0;
            width: 100%;
            height: 40%;
            background: #f98a08;
            opacity: 0.2;
            z-index: -1;
          }
        }
      }
      .collapse-content {
        margin-top: 14px;
        display: none;
        width: 100%;
        box-sizing: border-box;
        padding: 0 0 0 20px;
        p {
          margin: 10px 0;
          font-size: 14px;
          color: #333333;
          line-height: 1.5;
          text-align: justify;
        }
      }
    }
  }
  // 注意事项
  .notice {
    margin-top: 15px;
    .notice-info {
      margin-top: 5px;
      p {
        margin: 5px 0;
        font-size: 14px;
        color: #333333;
        line-height: 1.5;
        text-align: justify;
      }
    }
  }
  .download {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
    margin-bottom: 15px;
    .btn {
      font-size: 16px;
      color: #ffffff;
      background: #ff9800;
      border-radius: 5px;
      width: 180px;
      height: 35px;
      text-align: center;
      line-height: 35px;
    }
  }
}
</style>



